import { Component } from '@angular/core';
import { BreakpointObserver, Breakpoints } from '@angular/cdk/layout';
import { Observable } from 'rxjs';
import { map, shareReplay } from 'rxjs/operators';
import {Location} from '@angular/common';
import {ActivatedRoute} from '@angular/router';

@Component({
  selector: 'app-lanavbar',
  templateUrl: './lanavbar.component.html',
  styleUrls: ['./lanavbar.component.scss']
})
export class LanavbarComponent {

  static paginaActual = false

  get paginaActual(){
    return LanavbarComponent.paginaActual
  }

  isHandset$: Observable<boolean> = this.breakpointObserver.observe(Breakpoints.Handset)
    .pipe(
      map(result => result.matches),
      shareReplay()
    );

  constructor(private route:ActivatedRoute,  private _location: Location,private breakpointObserver: BreakpointObserver) {
  }

  toBack(){
    this._location.back();
  }


}
