import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import {HistoriaComponent} from './historia/historia.component';
import {InicioComponent} from './inicio/inicio.component';
import {LightShotPublicacionComponent} from './light-shot-publicacion/light-shot-publicacion.component';
import {HistoriasComponent} from './historias/historias.component';

const routes: Routes = [
  { path: '', component: InicioComponent },
  { path: 'publicacion/:foto', component: LightShotPublicacionComponent },
  { path: 'historias', component: HistoriasComponent },
];

@NgModule({
  imports: [RouterModule.forRoot(routes,{
    scrollPositionRestoration: 'top'
  })],
  exports: [RouterModule]
})
export class AppRoutingModule { }
