import { Component, OnInit ,AfterViewInit} from '@angular/core';
import {ActivatedRoute} from '@angular/router';
import {window} from 'rxjs/operators';
import {LanavbarComponent} from '../lanavbar/lanavbar.component';

@Component({
  selector: 'app-light-shot-publicacion',
  templateUrl: './light-shot-publicacion.component.html',
  styleUrls: ['./light-shot-publicacion.component.scss']
})
export class LightShotPublicacionComponent implements OnInit {


  esPantallaCompleta = false;
  foto =""

  constructor(private route:ActivatedRoute) {
    LanavbarComponent.paginaActual= true;

  }

  ngOnInit(): void {
    document.body.scrollTop = 0;


    this.route.params.subscribe(datos=>{
      this.foto = datos.foto
    })

  }


  ngAfterViewInit(): void {
  console.log("asd")
    document.body.scrollTop = 0;

    console.log(document.body)
    console.log(window)
  }






}
