import { Component, OnInit } from '@angular/core';
import {FotosInstagramService} from '../fotos-instagram.service';
import {LanavbarComponent} from '../lanavbar/lanavbar.component';

@Component({
  selector: 'app-historias',
  templateUrl: './historias.component.html',
  styleUrls: ['./historias.component.scss']
})
export class HistoriasComponent implements OnInit {

 /* host = 'http://localhost:5000';*/
  host = 'http://179.8.162.42:5000';
  path = "/static/stories/malibuu_storee2/";
  listaHistorias = ['das','asdasd']
  activo = ""
cc = 0;
  static activo = "";

  get elActivo(){
    return HistoriasComponent.activo
  }


  constructor() {
    LanavbarComponent.paginaActual= true;
    this.host = this.host+this.path;

  }

  ngOnInit(): void {

      this.listaHistorias = JSON.parse(localStorage.getItem('historias'));
    HistoriasComponent.activo = this.listaHistorias[this.cc]

  }

  esa(t){
    HistoriasComponent.activo = t;
  }

  sgte(){
    if(this.cc == 0){
      this.cc = this.listaHistorias.length-1;
      HistoriasComponent.activo = this.listaHistorias[this.cc]
    }else{
      this.cc--;
      HistoriasComponent.activo = this.listaHistorias[this.cc]
    }


  }
  atras(){
    if(this.cc == this.listaHistorias.length-1){
      this.cc = 0;
      HistoriasComponent.activo = this.listaHistorias[this.cc]
    }else{
      this.cc++;
      HistoriasComponent.activo = this.listaHistorias[this.cc]
    }
  }
  subirUno(){
    console.log("subiendo");
  }


}
